.chatContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  max-width: 600px;
  margin: 0 auto;
  border: 1px solid #ccc;
}

.chatHeader {
  background-color: #075e54;
  color: white;
  padding: 10px;
}

.messageList {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
}

.inputArea {
  display: flex;
  padding: 10px;
  background-color: #f0f0f0;
}

.messageInput {
  flex: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 20px;
  margin-right: 10px;
}

.sendButton {
  background-color: #128c7e;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 20px;
  cursor: pointer;
}

.sendButton:hover {
  background-color: #0c6b5d;
}